  margin-top: -424px; }
  @media (max-width: 1023px) {
    .video__photo-wrapper {
      margin-top: -197px; } }
  @media (max-width: 767px) {
    .video__photo-wrapper {
      margin-top: -113px; } }

.video__photo-wrapper img {
  margin: 0 auto; }

.video__photo-wrapper button {
  position: absolute;
  top: 50%;
  left: 50%;
  display: block;
  width: 65px;
  height: 65px;
  margin: auto;
  background: url("../../img/icon-play.svg") no-repeat;
  background-size: cover;
  border: 0;
  transform: translate(-50%, -50%);
  transition: 0.3s;
  transition-timing-function: ease-out; }
  .video__photo-wrapper button:hover, .video__photo-wrapper button:focus {
    transform: translate(-50%, -50%) scale(1.2); }
  .video__photo-wrapper button:active {
    transform: translate(-50%, -50%) scale(0.8); }
  @media (min-width: 768px) and (max-width: 1439px) {
    .video__photo-wrapper button {
      width: 91px;
      height: 91px; } }

